const template = `{ f: 270, l: 1, g: 1, p: 0 }

overtones = {
  O[
    (5/2, 0, 1/2, 0),
    (3/2, 2, 1, 1),
    (1/1, 0, 1, -1),
  ]
}

thing1 = {
  overtones |
  Sequence [
    Fm 1,
    Fm 9/8,
    Fm 5/4,
  ] 
  | ModBy [
    Seq [Fm 1/2, Fm 3/4,  Fm 1] | Repeat 7
  ]
}

thing2 = {
  Overlay [
    Fm 3, Fa 3 | Fm 3
  ]
  | Seq [Fm 1, Fm 5/6, Fm 7/8 | Lm 1/3]
  | Fm 5/4
  > FitLength thing1
}

main = {
  Overlay [
    thing1, 
    thing2
  ]
  | Seq [Fm 1, Fm 7/8]
  | Repeat 4
}
`;

export default template;
